/*
 *
 * Home Page
 *
*/

/* main color: rgb(235, 249, 255) */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'IBM Plex Sans', "Helvetica Neue", "Segoe UI",
        Helvetica, Verdana, Arial, sans-serif;
    color: #435472;
}

html {
    overflow-y: scroll;
    min-height: 100%;
    position: relative;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
    font-weight: 600;
}

.hidden {
    display: none;
}

.container {
    max-width: 570px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 15px;
    padding-left: 15px;
}

nav .container, footer .container {
    max-width: 570px;
    padding-right: 15px;
    padding-left: 15px;
}

.wrap-out {
    position: relative;
}

.wrap-out::before {
    content: '';
    position: absolute;
    right: 5px;
    top: 5px;
    left: -6px;
    bottom: -6px;
    background: rgb(235, 249, 255);
    z-index: 1;
}

.wrap-in {
    border: 2px solid rgb(211, 210, 210);
    position: relative;
    z-index: 2;
}

.thick-border {
    border-right: 10px solid rgb(211, 210, 210);
}

.js-focus-visible :focus:not(.focus-visible) {
    outline: none;
  }

/* fix footer to bottom */
nav {
    /* margin-bottom: calc(100vh - 158px); */
}

#app-container > section {
    margin-bottom: 120px;
    /* min-height: calc(100vh - 158px); */
    /* margin-top: calc(158px - 100vh); */
}


