@keyframes ld-blink {
    0% {
      opacity: 1;
    }
    49% {
      opacity: 1;
    }
    50% {
      opacity: 0;
    }
    100% {
      opacity: 0;
    }
}
@-webkit-keyframes ld-blink {
    0% {
        opacity: 1;
    }
    49% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}
.ld.ld-blink {
    -webkit-animation: ld-blink 1s infinite linear;
    animation: ld-blink 1s infinite linear;
}

.network-error-fixed {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    margin-bottom: 0;
    padding: 8px;
    z-index: 3;
    text-align: center;
    color: white;
    background-color: #d45c51;
}

.network-error-inline {
    text-align: center;
    margin-top: 40px;
}

.no-match {
    text-align: center;
}


/*
 * Navigation
*/

nav .wrap-in {
    border-width: 0;
    border-bottom: 1px solid rgb(211, 210, 210);
}

nav .container {
    position: relative;
    height: 50px;
}

nav .skip-link {
    position: absolute;
    z-index: 5;
    border: 1px solid rgb(211, 210, 210);
    background: whitesmoke;
    color: #435472;
    padding: 7px;
    top: -37px;
    cursor: pointer;
}

nav .skip-link:focus {
    top: 0;
}

nav .logo {
    display: inline-block;
    float: left;
    vertical-align: middle;
}

/* logo */
nav .logo a {
    display: block;
    padding: 8px;
    height: 34px;
    box-sizing: content-box;
}

nav .logo img {
    display: block;
}

nav .logo a:hover,
nav .logo a.focus-visible:focus {
    outline-width: 0px;
    /* background: white; */
    border-bottom: 3px solid rgb(211, 210, 210);
    transition-duration: 0.2s;
}

